import React from "react";
import { Box, Grid } from "@mui/material";
import MarketplaceCard from "./MarketplaceCard";
import { useDispatch, useSelector } from "react-redux";
import { getBuyCells } from "../../Api/api";
import { AddCeil, AddfailNfts } from "../../Redux/Reducer";
import Loading from "../../Loading/Loading";
import Comingsoon from "../modal/Comingsoon";
function MarketplacePage() {
  const dispatch = useDispatch();
  const [loading, setLoading] = React.useState(false);

  const init = React.useCallback(() => {
    (async function () {
      setLoading(true);
      let buyceilValue = await getBuyCells();
      dispatch(AddCeil(buyceilValue?.nfts));
      dispatch(AddfailNfts(buyceilValue?.failedNfts));
      setLoading(false);
    })();
  }, [dispatch]);
  const { ceilSlice } = useSelector((state) => state);
  const { ceildetails } = ceilSlice;
  React.useEffect(() => {
    init();
  }, [dispatch, init]);

  return (
    <>
      {loading && <Loading loading={loading} />}
      <Grid container spacing={2} p={2} mt="0px" sx={{ width: "100%" }}>
        {ceildetails?.length > 0 ? (
          ceildetails?.map(
            (
              {
                _id,
                lang,
                long,
                name,
                filelink,
                file,
                capturesImage,
                productTotalPrice,
                user,
                likesList,
                favoritesList,
                howManyViews,
                paidStatusContract,
              },
              index
            ) => (
              <>
                <Grid item xs={12} sm={6} md={3} key={_id + index} mt={12}>
                  <MarketplaceCard
                    locations={{ lang, long }}
                    id={_id}
                    name={name}
                    Image={capturesImage}
                    user={user}
                    filelink={filelink}
                    file={file}
                    productTotalPrice={productTotalPrice}
                    userlikeviews={{ likesList, favoritesList }}
                    howManyViews={howManyViews}
                    paidStatusContract={paidStatusContract}
                  />
                </Grid>
              </>
            )
          )
        ) : (
          <Box sx={{ mx: "auto", mt: 30 }}>
            <Comingsoon />
          </Box>
        )}
      </Grid>
    </>
  );
}

export default MarketplacePage;
